import React, { Component } from 'react'
import { Form, Input, Button, Icon, Checkbox, message} from 'antd'
import Header from 'components/loginHeader'
import RegisterPic from 'assets/images/common/register_bg.png'
import axios from 'axios'
import './index.less'
const FormItem = Form.Item

class Regist extends Component {
  constructor(props){
    super(props)
    this.state = {}
  }
  // 
  handleSubmit = (e) => {
    e.preventDefault()
    this.props.form.validateFields((err, values) => {
      if (!err) {
        const {username, password} = values
        this.setState({
          submitLoading: true
        })
        axios.post('/user/register', {username, password, nickname: 'nickname'})
        .then(rec => {
          this.setState({
            submitLoading: false
          })
          if(rec.data.code == 1){
            this.props.history.push("/signIn")
          }
        })
        .catch(err => {
          this.setState({
            submitLoading: false
          })
          this.props.form.setFields({
            password: {
              value: password,
              errors: [new Error(', ')]
            },
          })
        })
      }
    })
  }
  // 
  getVCode = () => {
    let username = this.props.form.getFieldsValue().username
    let vCode = this.props.form.getFieldsValue().vCode || ''
    if(!username){
      this.props.form.validateFields(['username'])
    }else{
      this.setState({
        getVCodeLoading: true
      })
      axios.post('/user/getMailVcode', {username, forCreate: true})
      .then(res => {
        this.setState({
          getVCodeLoading: false
        })
        if(res.data.code === 1 || res.data.message === 'Code has been send'){
          // this.props.form.setFields(
          this.setState({
            vCodeBtnDisable: true
          })
        }
        if(res.data.code !== 1){
          const message = res.data.message == 'Used mail address' ? '' : '( 30 )'
          const keys = res.data.message == 'Used mail address' ? 'username' : 'vCode'
          this.props.form.setFields({
            [keys]: {
              value: keys == 'username' ? username : vCode,
              errors: [new Error(message)]
            },
          })
        }
      })
      .catch(err => {
        this.setState({
          getVCodeLoading: false
        })
        this.props.form.setFields({
          username: {
            value:  username,
            errors: [new Error(', ')]
          },
        })
      })
    }
  }
  // 
  checkThePwd = (rule, value, callback) => {
    const reg = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,16}$/
    if(! reg.test(value)){
      callback('8~12')
    }
    callback()
    return
  }
  // 
  checkCheckbox = (rule, value, callback) => {
    if(!value){
      callback('')
    }
    callback()
    return
  }
  //  
  checkVCode = (rule, value, callback) => {
    let username = this.props.form.getFieldsValue().username
    if(username && value && value.length === 4){
      axios.post('/user/checkVcode',{username, vcode: value}).then(rec => {
        console.log(rec)
        if(rec.data.code == 1){
          callback()
        }else{
          callback('')
        }
      })
      return
    }
    callback() //''
    return
  }
  render() {
    const { getFieldDecorator } = this.props.form
    const {vcode, errInfo, vCodeBtnDisable, getVCodeLoading, submitLoading} = this.state
    return (
      <div className="register">
        <Header />
        <div className="registerContent">
          <div className="rigisterWrapper">
            <div className="leftContent fl">
              <h2></h2>
              <Form onSubmit={this.handleSubmit}>
                <FormItem >
                    {getFieldDecorator('username', {
                        rules: [{ 
                          required: true, 
                          message: '',
                          type: 'email'
                        }],
                        validateFirst: true
                    })(
                        <Input placeholder="" prefix={"  :"}/>
                    )}
                </FormItem>
                <FormItem className="registervCode">
                    {getFieldDecorator('vCode', {
                        rules: [{ 
                          required: true, 
                          message: ''
                        }, { validator: this.checkVCode }],
                    })(
                        <Input placeholder="" prefix={"  : "} className={getVCodeLoading && 'width240'}/>
                    )}
                    <Button loading={ getVCodeLoading} title={vCodeBtnDisable ? '' : ''} disabled={vCodeBtnDisable} onClick={this.getVCode}></Button>
                </FormItem>
                <FormItem>
                    {getFieldDecorator('password', {
                        rules: [{ 
                          required: true, 
                          message: '8~12' 
                        },{ validator: this.checkThePwd }],
                    })(
                        <Input type="password" placeholder="" prefix={"  : "}/>
                    )}
                </FormItem>
                <FormItem>
                    {getFieldDecorator('rePassword', {
                        rules: [{ 
                          required: true, 
                          message: ''
                        },{ validator: this.checkThePwd }],
                    })(
                        <Input type="password" placeholder="" prefix={": "}/>
                    )}
                </FormItem>
                <FormItem>
                  {getFieldDecorator('remember',{
                    valuePropName: 'checked',
                    initialValue: true,
                    rules: [{ validator: this.checkCheckbox }]
                  })(
                    <Checkbox></Checkbox>
                  )}
                  <label><a href="#"></a></label>
                </FormItem>
                <Button loading={submitLoading} type="primary" htmlType="submit" className="registerBtn"></Button>
              </Form>
              <p className="hasAccount"><Icon type="left" /> , <a href="/signIn"></a></p>
            </div>
            <div className="rightPic fl">
              <img src={RegisterPic} alt=""/>
            </div>
          </div>
          
        </div>
      </div>
    )
  }
}
const Register = Form.create()(Regist)
export default Register