      let nameNodeLive = nameNodeData.metrics.dfs ? JSON.parse(nameNodeData.metrics.dfs.namenode.DeadNodes).length : 'a / n'
      let nameNodeDead = nameNodeData.metrics.dfs ? JSON.parse(nameNodeData.metrics.dfs.namenode.LiveNodes).length : 'a / n'
      let nameNodeDecom = nameNodeData.metrics.dfs ? JSON.parse(nameNodeData.metrics.dfs.namenode.DecomNodes).length : 'a / n'
      let nameNodeTotal = nameNodeLive + nameNodeDead + nameNodeDecom


      let secondaryNameNode = serviceBaseInfo.SECONDARY_NAMENODE.host_components[0]
