const ConnSummaryPage = connect((state, { match, location }) => {
  return {
    loadLayout: state.summary.loadLayout,
    widgetLayoutInfo: state.summary.widgetLayoutInfo,
    loadingBaseInfo: state.summary.loadingBaseInfo,
    serviceBaseInfo: state.summary.serviceBaseInfo,
  }
})(SummaryPage)

export default ConnSummaryPage
