import React from 'react'
import {Menu} from 'antd'
const SubMenu = Menu.SubMenu;

function OperateMenu(props) {
  return (
    <Menu onClick={props.onChange} selectable={false} style={{ width: 160 }} mode="vertical" className="hostOperateMenu">
      {PublicMenu({ ...props, title: `(${props.choose || 0})`, disabled: props.choose ? false : true, keys: 'choose' })}
      {/* {PublicMenu({ ...props, title: `(${props.filter || props.all || 0})`, disabled: false, keys: 'filter' })} */}
      {PublicMenu({ ...props, title: `(${props.all || 0})`, disabled: false, keys: 'all' })}
    </Menu>
  )
}
export default OperateMenu


function PublicMenu(props) {
  return <SubMenu className="hostSubMenu" 
        key={props.keys} 
        disabled={props.disabled} 
        title={<span>{props.title}</span>}>
    <SubMenu key='host' title={<span><span></span></span>}>
      <Menu.Item key="startAll" data-text=""></Menu.Item>
      <Menu.Item key="stopAll" data-text=""></Menu.Item>
      <Menu.Item key="restartAll" data-text=""></Menu.Item>
      <Menu.Item key="reinstall" data-text=""></Menu.Item>
      <Menu.Item key="open" data-text="hosts"></Menu.Item>
      <Menu.Item key="close" data-text="hosts"></Menu.Item>
      <Menu.Item key="setRack" data-text="Rack">Rack</Menu.Item>
    </SubMenu>
    {/* <SubMenu key='DataNodes' title={<span><span>DataNodes</span></span>}>
      <Menu.Item key="start" data-text=" DataNodes"></Menu.Item>
      <Menu.Item key="stop" data-text=" DataNodes"></Menu.Item>
      <Menu.Item key="restart" data-text="restart"></Menu.Item>
      <Menu.Item key="add"></Menu.Item>
      <Menu.Item key="abandon"></Menu.Item>
      <Menu.Item key="reuse"></Menu.Item>
    </SubMenu>
    <SubMenu key='Regionservers' title={<span><span>Regionservers</span></span>}>
      <Menu.Item key="start" data-text=" Regionservers"></Menu.Item>
      <Menu.Item key="stop" data-text=" Regionservers"></Menu.Item>
      <Menu.Item key="restart" data-text="restart"></Menu.Item>
      <Menu.Item key="add"></Menu.Item>
      <Menu.Item key="abandon"></Menu.Item>
      <Menu.Item key="reuse"></Menu.Item>
    </SubMenu> */}
  </SubMenu>
}