import React, { Component } from 'react'
import { Table, Input, Checkbox, Progress, Tooltip, Button, Icon, Spin, Modal } from 'antd'
import { connect, actions } from 'mirrorx'
import OperateMenu from './operateMenu'
import { history } from 'utils'
import './index.less'
import NullCluster from 'pages/nullCluster'
import Operate from './operate'
import {allComponents} from 'config/allComponents'
import ConsoleHeader from 'components/consoleHeader'

class HostPage extends Component {
  constructor(props) {
    super(props)
    this.state = {
      selectedRowKeys: [],
      restartData: {}
    }
    this.host_components = ['startAll', 'stopAll']
    this.requests = ['restart']
  }
  async componentDidMount () {
    const { hostLists } = this.props
    // if(currentCluster){
    //   !hostLists && actions.host.getHostList({clusterName: currentCluster.clusterName})
    // }else{
    await actions.app.getClusterList().then(rec => {
      if(rec.length == 0){
        history.push('/noCluster')
      }
      if(rec && rec.length > 0){
        !hostLists && actions.host.getHostList({clusterName: rec[0].clusterName})
        this.timer = setInterval(() => {
          const {currentCluster} = this.props
          actions.host.getHostList({clusterName: currentCluster.clusterName})
        }, 4000)
      }
    })

  }
  componentWillUnmount (){
    clearInterval(this.timer)
    this.timer = null
  }
  clusterChange = (val) => {
    this.setState({
      loading: true
    })
    actions.host.getHostList({clusterName: val.clusterName})
    .then(rec => {
      this.setState({
        loading: false
      })
    })
  }

  chooseHost = (hostInfo) => {
    actions.host.setCurrentHost(hostInfo).then(async res => {
      const host_name = hostInfo.Hosts.host_name
      // this.setState({loading: true})
      // await Promise.all([actions.host.getHostSummary({host_name}), actions.host.getHostStatus({host_name})])
      // this.setState({loading: false})
      clearInterval(this.timer)
      this.timer = null
      this.props.history.push(`hosts/${host_name}`)
    })
  }

  columns = () => {
    return [{
      title: '',
      key: 'Hosts.host_name',
      render: (record) => {
        const { CRITICAL, UNKNOWN, WARNING } = record.alerts_summary
        let tooltipTitle = ''
        let iconType = 'check-circle'
        const obj = this.formatData(record.host_components)
        const isMaster = obj.master.length > 0 ? 'master' : 'slave'
        if (CRITICAL > 0) {
          tooltipTitle = `${isMaster}: \r\n ${obj[isMaster].join(', ')}`
          iconType = isMaster == 'master' ? 'warning' : "minus-circle"
        } //else if (WARNING > 0) {
          // tooltipTitle = 'xxx, xxx '
          // iconType = 'warning'
        // } else if (UNKNOWN > 0) {

        // }
        if(record.Hosts.maintenance_state == 'ON'){
          tooltipTitle = ''
          iconType = 'medicine-box'
        }
        return <span className="goHostDetail" onClick={this.chooseHost.bind(this, record)}>
          <Tooltip overlayClassName="progressTooltip" placement="right" title={tooltipTitle} getPopupContainer={()=>document.querySelector('.goHostDetail')}>
            <Icon type={iconType} className={'stateIcon ' + iconType} />
          </Tooltip>
          <span>{record.Hosts.host_name}</span>
        </span>
      }
    }, {
      title: 'IP',
      dataIndex: 'Hosts.ip',
    }, {
      title: 'Pack',
      dataIndex: 'Hosts.rack_info',
    }, {
      title: '',
      // dataIndex: '',
      key: 'Hosts.ph_cpu_count',
      render: (record) => {
        return <span>{record.Hosts.cpu_count}({record.Hosts.ph_cpu_count})</span>
      }
    }, {
      title: 'RAM',
      dataIndex: 'Hosts.total_mem',
      render: (record) => {
        return <span>{(record / 1024 / 1024).toFixed(2)}GB</span>
      }
    }, {
      title: '',
      dataIndex: 'metrics.disk',
      render: (record) => {
        if (!record) {
          return <Tooltip overlayClassName="progressTooltip" placement="top" title=''>
            <Progress className="diskPercent" percent={0} showInfo={false} />
          </Tooltip>
        }
        const used = record.disk_total - record.disk_free
        const percent = used / record.disk_total * 100
        return <Tooltip overlayClassName="progressTooltip" placement="top" title={`${used.toFixed(2)}GB/${record.disk_total.toFixed(2)}GB(${percent.toFixed(2)}%)`}>
          <Progress className="diskPercent" percent={+percent.toFixed(2)} showInfo={false} />
        </Tooltip>
      }
    }, {
      title: '',
      dataIndex: 'metrics.load.load_one',
      render: text => text || text == 0 ? <span style={{'paddingLeft': '6px'}}>{ text }</span> : <Tooltip placement="left" title=", Metrics"><Icon style={{color: 'red', 'fontSize': '20px'}} type="question"/></Tooltip>
    }, {
      title: '',
      dataIndex: 'stack_versions',
      render: (record) => {
        const version = record[0].repository_versions["0"].RepositoryVersions.display_name
        return <span>{version}</span>
      }
    }, {
      title: '',
      dataIndex: 'host_components',
      render: (record) => {
        return <span className="lookAtComponents" onClick={this.showAllComponents(record)}>{record.length} </span>
      }
    }];
  }
  // 
  formatData = (component) => {
    let slave = []
    let master = []
    component.map(rec => {
      const item = rec.HostRoles
      if(item.state == 'INSTALLED'){
        if(allComponents[item.component_name].category != 'MASTER'){
          slave.push(item.display_name)
        }else{
          master.push(item.display_name)
        }
      }
    })
    return {master, slave}
  }

  showAllComponents = (components) => {
    return () => {
      this.setState({ 
        host_components: components, 
        modalTile: '',
        modalClass: 'componentsModal' 
      })
    }

  }

  closeModal = () => {
    this.setState({ host_components: null })
  }

  //  1:    2:   0: 
  getComponentData = (data, type = 0) => {
    let components = []
    const hostsStr = data.map(rec => rec.Hosts.host_name).join(',')
    if(type == 1){
      data && data.map(rec => {
        const host_components = rec.host_components
        const h = rec.Hosts.host_name
        host_components && host_components.map(item => {
          if(JSON.stringify(components).includes())
          components.push({
            service_name: item.HostRoles.service_name,
            component_name: item.HostRoles.component_name,
            hosts: h
          })
        })
      })
    }else if (type == 2){
      data && data.map(rec => {
        let curStr = ''
        const host_components = rec.host_components
        host_components && host_components.map(item => {
          curStr += item.HostRoles.component_name
        })
        components.push(curStr)
      })
      // components = components.join('|')
    }else{
      components = hostsStr
    }
    return {data: components, hostsStr }
  }
  // 
  menuChange = (e) => {
    const operateType = e.keyPath
    const text = e.domEvent.target.dataset.text || e.domEvent.target.getAttribute('data-text')
    const { hostLists } = this.props
    const { selectedRowKeys, chooseHost } = this.state
    let curHost = []
    if (operateType[2] == 'choose' && selectedRowKeys && selectedRowKeys.length > 0) {
      hostLists.map(host => {
        if (selectedRowKeys.includes(host.Hosts.host_name)) {
          curHost.push(host)
        }
      })
    } else if(operateType[2] == 'filter') {
      curHost = chooseHost
    } else {
      curHost = hostLists
    }
    let setState = {
      operateText: text,
      operateData: curHost,
      operateType: operateType
    }
    if(operateType[0] == 'setRack'){
      setState.setRackModal = true
    }
    this.setState(setState, () => {
      this.rackIpt && this.rackIpt.focus()
    })
  }
  // 
  modalSureHandle = () => {
    const {operateData, operateText, operateType} = this.state
    const type = operateType[0]
    const category = operateType[1]
    if(type.indexOf('start') == 0 || type.indexOf('stop') >= 0){
      const {data, hostsStr} = this.getComponentData(operateData, 2)
      actions.host.postOperate({
        state: type.indexOf('start') >= 0 ? 'STARTED' : 'INSTALLED',
        clusterName: operateData[0].Hosts.cluster_name,
        context: operateText,
        hostsStr: hostsStr,
        components: data,
        level: category == 'host' ? 'CLUSTER' : 'SERVICE'
      }).then(rec => {
        console.log(rec)
      })
    }
    if(type == 'reinstall'){
      const {data, hostsStr} = this.getComponentData(operateData, 0)
      actions.host.installFailedComponents({
        clusterName: operateData[0].Hosts.cluster_name,
        context: operateText,
        hostsStr: hostsStr
      }).then(rec => {
        console.log(rec)
      })
    }
    if(category == 'DataNode' || category == 'Regionserver'){
      this.setState({
        curOperateService: category
      })
    }
    if(type == 'open' || type == 'close'){
      const {data, hostsStr} = this.getComponentData(operateData, 1)
      actions.host.openOrCloseMaintenance({
        clusterName: operateData[0].Hosts.cluster_name,
        context: operateText,
        hostsStr: hostsStr,
        isOpen: type == 'open' ? 'ON' : 'OFF'
      }).then(rec => {
        console.log(rec)
      })
    }
    const {currentCluster} = this.props
    actions.host.getHostList({clusterName: currentCluster.clusterName})
  }
  closeRestartModal = () => {
    this.setState({curOperateService: null, operateText: null})
  }
  restartSure = () => {
    this.setState({curOperateService: null, operateText: null})
  }
  formChange = (key,e) => {
    let {restartData} = this.state
    const val = e.target.checked || e.target.value
    restartData[key] = val
    this.setState({
      restartData
    })
  }
  rackChange = (e) => {
    this.setState({
      newRack: e.target.value
    })
  }
  setRackSure = () => {
    const rack = this.state.newRack
    if(!rack){
      // ...
      return
    }
    const {operateData, operateText, operateType} = this.state
    const {hostsStr} = this.getComponentData(operateData, 0)
    this.setState({loading: true})
    actions.host.setHostRack({
      clusterName: operateData[0].Hosts.cluster_name,
      context: operateText,
      hostsStr: hostsStr,
      rack,
    }).then(async rec => {
      await actions.host.getHostList({clusterName: operateData[0].Hosts.cluster_name})
      this.setState({
        setRackModal: false,
        operateText: null,
        loading: false
      })
    })
  }
  closeRackModal = () => {
    this.setState({
      setRackModal: false,
      operateText: null
    })
  }
  closeOerateModal = () => {
    this.setState({
      operateText: null
    })
  }
  rowSelection = () => ({
    onChange: (selectedRowKeys, selectedRows) => {
      this.setState({
        selectedRowKeys,
        chooseHost: selectedRows
      })
    },
    selectedRowKeys: this.state.selectedRowKeys
  })

  render() {
    const { hostLists, clusterList, currentCluster } = this.props
    const { selectedRowKeys, loading, restartData, curOperateService, setRackModal, newRack, host_components, chooseHost, operateText } = this.state
    const theOperateHost = chooseHost || hostLists

    if(clusterList && clusterList.length == 0){
      return <NullCluster />
    }
    if(!currentCluster){
      return <Spin spinning={true} style={{width: '100%', height: '100%'}}/>
    }
    return (
      <div className="hostManage">
        <ConsoleHeader selectChange={this.clusterChange}/>
        <Spin spinning={loading || !hostLists} wrapperClassName="hostSpin">
          <div className="hM_content">
            <Operate hostLists={hostLists} selectedRowKeys={selectedRowKeys} chooseHost={chooseHost}/>
            <Table bordered
              dataSource={hostLists}
              columns={this.columns()}
              rowSelection={this.rowSelection()}
              rowKey={(record) => record.Hosts.host_name}
              Pagination={false} />
          </div>
        </Spin>
        <Modal
          title=""
          className="componentsModal"
          visible={host_components ? true : false}
          onCancel={this.closeModal}
          footer={
            <Button type="primary" onClick={this.closeModal}></Button>
          }
        >
          <p>{host_components && host_components[0].HostRoles.host_name}</p>
          <ul>
            {
              host_components && host_components.map(res => {
                return <li key={res.href}>{res.HostRoles.component_name}</li>
              })
            }
          </ul>
        </Modal>
        <Modal
          title=""
          className="hostOperateModal"
          visible={operateText && !setRackModal ? true : false}
          onCancel={this.closeOerateModal}
          footer={
            <Button type="primary" onClick={this.modalSureHandle}></Button>
          }
        >
          {theOperateHost && theOperateHost.length}<b>{operateText}</b>?
          <ul>
            {
              theOperateHost && theOperateHost.map(rec => {
                return <li key={rec.href}>{rec.Hosts.host_name}</li>
              })
            }
          </ul>
        </Modal>
        <Modal
          title="Rack"
          className="setRackModal"
          visible={setRackModal}
          onCancel={this.closeRackModal}
          footer={
            <Button loading={loading} type="primary" onClick={this.setRackSure}></Button>
          }
        >
          <div>
            <label>Rack:</label> 
            <Input ref={(el) => this.rackIpt = el} value={newRack} onChange={this.rackChange}/>
          </div>
        </Modal>
        <Modal
          title={` ${curOperateService}s`}
          className="restartServiceModal"
          visible={!!curOperateService}
          onCancel={this.closeRestartModal}
          footer={
            <Button type="primary" onClick={this.restartSure}></Button>
          }
        >
          <div>
            <div className="tips">
              <p> {curOperateService}</p>
              <p>: . HDFS</p>
            </div>
            <div className="row times">
              <label>:</label> 
              <Input onChange={this.formChange.bind(this, 'restart')} value={restartData.restart}/>
              {curOperateService}/
            </div>
            <div className="row wait">
              <label>:</label> 
              <Input onChange={this.formChange.bind(this, 'wait')} value={restartData.wait}/>
              
            </div>
            <div className="row send">
              <label>:</label> 
              <Input onChange={this.formChange.bind(this, 'send')} value={restartData.send}/>
              
            </div>
            <div className="row checked">
              <Checkbox onChange={this.formChange.bind(this, 'overdue')} checked={restartData.overdue}>DataNode</Checkbox>
              <Checkbox onChange={this.formChange.bind(this, 'HDFS')} checked={restartData.HDFS}>HDFS</Checkbox>
            </div>
          </div>
        </Modal>
      </div>
    )
  }
}

const Hosts = connect(state => {
  return {
    hostLists: state.host.hostLists,
    clusterList: state.app.clusterList,
    currentCluster: state.app.currentCluster
  }
})(HostPage)

export default Hosts 